import React, { useState, useRef } from "react";
import ReactDOM from "react-dom";

class Timer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      elapsedTime: 0
    };

    this.countUp = this.countUp.bind(this);
    this.startCounting = this.startCounting.bind(this);
    setInterval(this.countUp, 1000);
  }

  startCounting() {}

  countUp() {
    if (this.props.pause != true) {
      this.setState(({ elapsedTime }) => ({ elapsedTime: elapsedTime + 1 }));
    }
  }

  render() {
    if (this.props.pause != true) {
      return (
        <div className="chickenTitle2">
          <h3>{this.state.elapsedTime} â²</h3>
          <p>second(s) on the egg timer</p>
        </div>
      );
    } else {
      return (
        <h3 className="chickenTitle2">
          It took you {this.state.elapsedTime} seconds to become the big ğŸ— !
        </h3>
      );
    }
  }
}

export default Timer;
