import React, { useState } from "react";

function Form(props) {
  const [formies, setFormies] = useState("small cat");

  const handleSubmit = (event) => {
    event.preventDefault();
    console.log("Form - handleSubmit", formies);
    props.valForm(formies);
  };
  const handleChange = (event) => {
    console.log("handleChange - event", event.target.value, event.target.name);
    setFormies(event.target.value);
  };

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <input
          type="text"
          className="form-control"
          name="gifSearch"
          placeholder="Search to get gifff..."
          onChange={handleChange}
        />
        <button className="btn btn-lg btn-primary btn-block">Submit</button>
      </form>
    </div>
  );
}

export default Form;
